import React from "react";
import {
  FaFacebookF,
  FaTwitter,
  FaInstagram,
  FaLinkedinIn,
  FaPinterestP,
  FaShoppingCart,
  FaMoneyBillWaveAlt,
} from "react-icons/fa";

const SocialShare = [
  { Social: <FaFacebookF />, link: "https://www.facebook.com/p.h.dee.dragqueen/" },
  { Social: <FaTwitter />, link: "https://twitter.com/phdee__" },
  { Social: <FaInstagram />, link: "https://www.instagram.com/phdee__/" },
  { Social: <FaShoppingCart />, link: "https://phdee.threadless.com/" },
  { Social: <FaMoneyBillWaveAlt />, link: "venmo://paycharge?txn=pay&recipients=phdeedragqueen" },
];

const Footer = () => {
  return (
    <>
      <div className="row align-items-center">
        <div className="col-md-6 my-2">
          <div className="nav justify-content-center justify-content-md-start">
            {SocialShare.map((val, i) => (
              <a key={i} href={`${val.link}`} rel="noreferrer" target="_blank">
                {val.Social}
              </a>
            ))}
          </div>
          {/* End .nav */}
        </div>
        {/* End .col */}

        <div className="col-md-6 my-2 text-center text-md-end">
          <p>
            © {new Date().getFullYear()} Copyright{" "} P.H. Dee
            {/* <a
              href="https://themeforest.net/user/ib-themes/portfolio"
              target="_blank"
              rel="noreferrer"
            >
              ib-themes
            </a>{" "} */}
          </p>
        </div>
        {/* End .col */}
      </div>
      {/* End .row */}
    </>
  );
};

export default Footer;
