import React, { useState } from "react";
import Scrollspy from "react-scrollspy";
import { Link } from "react-router-dom";
import ReactTooltip from "react-tooltip";
import {
  FiUser,
  FiBriefcase,
  FiFileText,
  FiPhoneOutgoing,
  FiCalendar,
} from "react-icons/fi";
import { FaHome, FaBlog, FaPhotoVideo, FaMailBulk } from "react-icons/fa";

const Header = () => {
  const [click, setClick] = useState(false);
  const handleClick = () => setClick(!click);

  return (
    <>
      {/* Header */}
      <div className="mob-header">
        <button className="toggler-menu" onClick={handleClick}>
          <div className={click ? "active" : ""}>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </button>
      </div>
      {/* End Header */}

      {/* nav bar */}
      <header className={click ? "header-left menu-open" : "header-left "}>
        <div className="scroll-bar">
          {/* <div className="hl-top">
            <div className="hl-logo">
              <Link to="/"></Link>
            </div>
          </div> */}
          {/* End htl-top */}

          <Scrollspy
            className="nav nav-menu"
            items={["home", "about", "calendar", "gallery", "booking"]}
            currentClassName="active"
            offset={-30}
          >
            <li>
              <a
                className="nav-link"
                href="#home"
                data-tip
                data-for="HOME"
                onClick={handleClick}
              >
                <FaHome />
                <br></br>
                <font size="1">Home</font>
                <ReactTooltip id="HOME" place="top" type="dark" effect="float">
                  <span>Home</span>
                </ReactTooltip>
              </a>
            </li>
            <li>
              <a
                className="nav-link"
                href="#about"
                data-tip
                data-for="ABOUT"
                onClick={handleClick}
              >
                <FiUser />
                <br></br>
                <font size="1">About P.H. Dee</font>
                <ReactTooltip id="ABOUT" place="top" type="dark" effect="float">
                  <span>About</span>
                </ReactTooltip>
              </a>
            </li>
            <li>
              <a
                className="nav-link"
                href="#calendar"
                data-tip
                data-for="calendar"
                onClick={handleClick}
              >
                <FiCalendar />
                <br></br>
                <font size="1">Calendar</font>
                <ReactTooltip id="CALENDAR" place="top" type="dark" effect="float">
                  <span>Calendar</span>
                </ReactTooltip>
              </a>
            </li>
            <li>
              <a
                className="nav-link"
                href="#gallery"
                data-tip
                data-for="Gallery"
                onClick={handleClick}
              >
                <FaPhotoVideo />
                <br></br>
                <font size="1">Gallery</font>
                <ReactTooltip id="Gallery" place="top" type="dark" effect="float">
                  <span>Gallery</span>
                </ReactTooltip>
              </a>
            </li>
            {/* <li>
              <a
                className="nav-link"
                href="#blog"
                data-tip
                data-for="BLOG"
                onClick={handleClick}
              >
                <FaBlog />
                <ReactTooltip id="BLOG" place="top" type="dark" effect="float">
                  <span>Blog</span>
                </ReactTooltip>
              </a>
            </li> */}
            <li>
              <a
                className="nav-link"
                href="#booking"
                data-tip
                data-for="CONTACT"
                onClick={handleClick}
              >
                <FaMailBulk />
                <br></br>
                <font size="1">Booking Info</font>
                <ReactTooltip
                  id="CONTACT"
                  place="top"
                  type="dark"
                  effect="float"
                >
                  <span>Bookings</span>
                </ReactTooltip>
              </a>
            </li>
          </Scrollspy>
        </div>
      </header>
      {/* End Header */}
    </>
  );
};

export default Header;
